.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.1px solid lightgray;
  padding: 10px 0px;
  width: 100%;
  z-index: 999;
  background-color: #fff;
}
.header__left {
  display: flex;
}
.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}
.header__search {
  display: flex;
  padding: 10px;
  align-items: center;
  border-radius: 5px;
  height: 22px;
  color: gray;
  background-color: rgb(231, 231, 231);
}
.header__search > input {
  background: none;
  outline: none;
  border: none;
  padding: 0px 30px;
}
.header__right {
  display: flex;
}
